import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

function App() {
  return (
    <Router>
      <div>
        <nav>
          <ul>
            <li>
              <Link to="/"><button to="/"> Home </button></Link>
            </li>
            <li>
              <Link to="/about"><button to="/about"> About </button></Link>
            </li>
            <li>
              <Link to="/users"><button to="/users"> Users </button></Link>
            </li>
            <li>
              <Link to="/fb"><button to="/fb"> fb </button></Link>
            </li>
            <li>
              <Link to="/gg"><button to="/gg"> gg </button></Link>
            </li>
          </ul>
        </nav>
        {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
        <Switch>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/users">
            <Users />
          </Route>
          <Route path="/fb">
            <Fb />
          </Route>
          <Route path="/gg">
            <Gg />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

function Home() {
  return <h2>Home</h2>;
}

function About() {
  return <h2>About</h2>;
}

function Users() {
  return <h2>Users</h2>;
}

function Fb() {
  return <h2>fb</h2>;
}

function Gg() {
  return <h2>gg</h2>;
}
export default App;